import React from 'react'
import ReactModal from 'react-modal'
import { connect } from 'react-redux'
import { EditComponent } from 'rendering'
import { get } from 'constants/lodash'
import styles from './styles.module.css'

export const EditElementModal = props => {
  if (props.data) {
    return (
      <ReactModal
        className={styles.FullModal}
        isOpen={props.isOpen}
        onRequestClose={props.onRequestClose}
      >
        <EditComponent
          onRequestClose={props.onRequestClose}
          id={props.id}
          data={props.data}
        />
      </ReactModal>
    )
  }
  return null
}

const mapStateToProps = state => {
  const id = state.uistate.selectedElement
  const data = get(state.document, id, null)
  return ({ id, data })
}

export default connect(mapStateToProps)(EditElementModal)