import React from 'react'
import { nonTerminal, terminal, childless } from './renderTypes'
import { Text, Plain } from './renderTypes/Text'

export const parseObject = function(object) {
  const [ type, ...others ] = object
  if (others.length) {
    if (others[0].constructor.name === 'Object') {
      const [props, ...children] = others
      return { type, props, children }
    } else {
      return { type, props: {}, children: others }
    }
  }
  return { type , props: {}, children: [] }
}

const isArray = Array.isArray

export const renderObject = function(data, {key}={}) {
  if (isArray(data)) {
    const {type, props, children} = parseObject(data)

    if (isArray(type)) {
      return React.createElement(React.Fragment, null, ...data.map(c => renderObject(c)))
    }
    if (childless[type]) {
      return React.createElement(childless[type], {key, ...props})
    }
    if (terminal[type]) {
      return React.createElement(terminal[type], {key,...props, data: children.join('\n')})
    }
    if (nonTerminal[type]) {
      return React.createElement(nonTerminal[type], {key, ...props}, ...children.map(c => renderObject(c)))
    }
  }
  else if (typeof data === 'string') {
    return <Text data={data} />
  }
  else if (typeof data === 'number') {
    return <Plain data={data.toString()} />
  }
  else if (typeof data === 'boolean') {
    return <Plain data={data.toString()} />
  }
  else if (data === null || data === undefined) {
    return null
  }
  console.error(`cannot render ${JSON.stringify(data)}`)
  return null
}