const simplifyCircuit = (logic, ops) => simplify(logic, {
  ifThen: true,
  iff: true,
  deMorgans: true,
  doubleNegative: true,
  combineAnd: true,
  combineOr: false,
  ...ops
})

function requiredSpace(log) {
  const logic = simplifyCircuit(log)
  if (typeof logic === 'string') {
    return {
      width: 2*NODERADIUS + 2*HGAP, 
      height: 2*NODERADIUS + 2*VGAP, 
      above: NODERADIUS + VGAP, 
      below: NODERADIUS + HGAP,
    }
  }
  if (isArray(logic)) {
    const [op, ...args] = logic
    if (op === 'Not') {
      return {
        width: 2*NODERADIUS + 2*HGAP, 
        height: 2*NODERADIUS + 2*VGAP, 
        above: NODERADIUS + VGAP, 
        below: NODERADIUS + HGAP,
      }
    }
    if (op === 'And') {
      let w = 0, a = 0, b = 0, h = 0
      for (const x of args) {
        const req = requiredSpace(x)
        w += req.width
        if (req.height > h) {
          h = req.height
        }
        if (req.above > a) {
          a = req.above
        }
        if (req.below > b) {
          b = req.below
        }
      }
      return {width: w, height: h, above: a, below: b}
    }
    if (op === 'Or') {
      let w = 0, h = 0
      for (const x of args) {
        const req = requiredSpace(x)
        if (req.width > w) {
          w = req.width
        }
        h += req.height
      }
      w += 2*HGAP
      //h += (this.args.length - 1)*VGAP
      return {width: w, height: h, above: h/2, below: h/2}
    }
  }
}

function draw(logic, {start={x: 0, y: 0}, availableWidth}={}) {
  if (typeof logic === 'string') {
    const {x=0, y=0} = start
    const extra = availableWidth ? (availableWidth - requiredSpace(logic).width)/2 : 0
    const cx = x + HGAP + NODERADIUS + extra
    return [
      ['Path', {points: `${x},${y} ${cx-NODERADIUS},${y}`}],
      ['Circle', {cx, cy: y, r: NODERADIUS}],
      ['Path', {points: `${cx+NODERADIUS},${y} ${x+2*HGAP+2*NODERADIUS+2*extra},${y}`}],
      ['Overlay', {x: cx, y: .1+y}, '$$' + logic + '$$'],
    ]
  }
  if (isArray(logic)) {
    const [op, ...args] = logic
    if (op === 'Not') {
      if (args.length === 1 && typeof args[0] === 'string') {
        return draw('\\lnot ' + args[0], {start, availableWidth})
      }
    }
    if (op === 'And') {
      let {x, y} = start
      const extra = availableWidth ? availableWidth - requiredSpace(logic).width : 0
      const between = extra / (args.length+1)
      const connections = [
        ['Path', {points: [[x, y], [x+between, y]]}]
      ]
      x += between
      let parts = []
      for (const a of args) {
        parts = parts.concat(draw(a, {start: {x, y}}))
        x += requiredSpace(a).width
        connections.push(['Path', {points: [[x,y], [x+between, y]]}])
        x += between
      }
      return [...connections, ...parts]
    }
    if (op === 'Or') {
      let {x, y} = start
      const req = requiredSpace(logic)
      const width = availableWidth ? availableWidth : req.width
      const extraW = HGAP + (width - req.width)/2
      const lines = [
        ['Path', {points: `${x},${y} ${x+extraW},${y}`}],
        ['Path', {points: `${x+width-extraW},${y} ${x+width},${y}`}]
      ]
      x += extraW

      const ys = []
      y += req.above
      for (const a of args) {
        const req2 = requiredSpace(a)
        y -= req2.above
        ys.push(y)
        y -= req2.below
        //y -= VGAP
      }
      lines.push(['Path', {
          points: [
            [x, ys[0]], 
            [x, ys[ys.length-1]],
          ],
      }])
      let parts = []
      for (let i = 0; i < args.length; i++) {
        parts = parts.concat(draw(args[i], {
          start: {x, y: ys[i]}, 
          availableWidth: width - 2*extraW
        }))
      }
      x += width - 2*extraW
      lines.push(['Path', {
        points: [
          [x, ys[0]], 
          [x, ys[ys.length-1]],
        ],
      }])

      return [...lines, ...parts]
    }
  }
  console.log(logic)
  console.error('Cannot render circuit')
  return []
}


function drawCircuit(logic, options={}) {
  const simp = simplifyCircuit(logic)
  const {width, above, below} = requiredSpace(simp)
  return [
    'CoordinatePlane', 
    {
      span: [-HGAP, -below-VGAP, +width+HGAP, +above+VGAP], 
      axis: false, 
      grid: false, 
      style: 'circuit',
      ...options,
    },
    ['Point', {x: 0, y: 0}],
    ...draw(simp, {start: {x:0,y:0}}),
    ['Point', {x: width, y: 0}]
  ]
}

export default drawCircuit

export {
  requiredSpace,
  simplifyCircuit,
}