import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import styles from './styles.module.css'
import { updateGraphStyle } from 'actions/style'
import { setAutoPagination, setPresentationZoom } from 'actions/config'
import SingleQuestionView from './SingleQuestionView'
import SlideView from './SlideView'

class PresentationView extends SlideView {
  static propTypes = {
    order: PropTypes.arrayOf(PropTypes.number).isRequired,
    content: PropTypes.object.isRequired,
    updateGraphStyle: PropTypes.func,
    setAutoPagination: PropTypes.func,
  }

  constructor(props) {
    super(props)
    this.state = {
      currentIndex: 0,
      showAnswer: false,
      scaleFactor: props.scaleFactor || 1.2,
    }
  }

  canGoBack = () => {
    if (this.state.currentIndex > 0) {
      return true
    }
    return false
  }
  canGoForward = () => {
    if (this.state.currentIndex < this.props.order.length-1) {
      return true
    }
    return false
  }

  problemNumber() {
    return +this.props.startNumbering + this.state.currentIndex
  }

  next = () => {
    let currentIndex = this.state.currentIndex + 1
    if (currentIndex >= this.props.order.length) {
      currentIndex -= this.props.order.length
    }
    this.setState({ currentIndex, showAnswer: false })
  }
  previous = () => {
    let currentIndex = this.state.currentIndex - 1
    if (currentIndex < 0) {
      currentIndex += this.props.order.length
    }
    this.setState({ currentIndex, showAnswer: false })
  }

  renderSlide() {
    return (
      <div className={styles.Slide}>
        {this.props.order.length ? (
          <SingleQuestionView 
            content={this.props.content[this.props.order[this.state.currentIndex]]}
            scaleFactor={this.state.scaleFactor}
            showAnswer={this.state.showAnswer}
            number={this.state.collapsed 
              ? this.problemNumber() 
              : undefined}
          />
        ) : null}
      </div>
    )
  }

}

const mapStateToProps = state => ({
  order: state.document.order,
  content: state.document.content,
  startNumbering: state.document.startNumbering,
  graphScaleFactor: state.style.graph.scaleFactor,
  scaleFactor: state.config.presentationZoom / 100,
})
const mapDispatchToProps = dispatch => ({
  setAutoPagination: bool => dispatch(setAutoPagination(bool)),
  setGraphScaleFactor: scaleFactor => dispatch(updateGraphStyle({scaleFactor})),
  setPresentationScaleFactor: scaleFactor => dispatch(setPresentationZoom(scaleFactor*100)),
})

export default connect(mapStateToProps, mapDispatchToProps)(PresentationView)