function latex(logic, {group=false}={}) {
  if (typeof logic === 'string') {
    return logic
  }
  if (Array.isArray(logic)) {
    const [op, ...args] = logic
    if (group && op !== 'Not') {
      return '\\left(' + latex(logic, {group: false}) + '\\right)' 
    }
    const argmap = a => latex(a, {group: true})
    if (op === 'Not') {
      return '\\lnot ' + argmap(args[0])
    }
    if (op === 'And' || op === 'Or') {
      const joiner = {And: ' \\land ', Or: ' \\lor '}[op]
      return args.map(argmap).join(joiner)
    }
    if (op === 'Then') {
      return argmap(args[0]) + ' \\implies ' + argmap(args[1])
    }
    if (op === 'Equiv') {
      return argmap(args[0]) + ' \\iff ' + argmap(args[1])
    }
    console.error('No latex method for ' + logic)
    return ' ??? '
  }
}

export default latex