import { reduce } from 'constants/lodash'

function evaluate(logic, vars) {
  if (typeof logic === 'string') {
    return vars[logic]
  }
  if (Array.isArray(logic)) {
    const [op, ...args] = logic
    if (op === 'Not') {
      return !evaluate(args[0], vars)
    }
    if (op === 'And') {
      return reduce(args, (a,b) => evaluate(a, vars) && evaluate(b, vars))
    } 
    if (op === 'Or') {
      return reduce(args, (a,b) => evaluate(a, vars) || evaluate(b, vars))
    }
    if (op === 'Then') {
      if (evaluate(args[0], vars) && !evaluate(args[1], vars)) {
        return false
      }
      return true
    }
    if (op === 'Equiv') {
      return evaluate(args[0], vars) === evaluate(args[1], vars)
    }
    return undefined
  }
}

export default evaluate