const isArray = Array.isArray

function getVariables(logic) {
  if (typeof logic === 'string') {
    return {[logic]: logic}
  }
  if (Array.isArray(logic)) {
    const args = logic.slice(1)
    const result = {}
    for (const x of args) {
      for (const k in getVariables(x)) {
        result[k] = k
      }
    }
    return result
  }
  return {}
}

function variableList(logic, {sort=true}={}) {
  const vars = Object.keys(getVariables(logic))
  if (sort) {
    return vars.sort()
  }
  return vars
}

function addVariableCombos(v, prev=[{}]) {
  return [
    ...prev.map(p => ({...p, [v]: true})),
    ...prev.map(p => ({...p, [v]: false}))
  ]
}

function allValueCombos(logic) {
  let result = [{}]
  for (const v of variableList(logic).reverse()) {
    result = addVariableCombos(v, result)
  }
  return result
}

function getOperations(logic) {
  if (typeof logic === 'string') {
    return {}
  }
  if (isArray(logic)) {
    const [op, ...args] = logic
    const result = {[op]: op}
    for (const x of args) {
      for (const k in getOperations(x)) {
        result[k] = k
      }
    }
    return result
  }
}

function operationList(logic, {sort=true}={}) {
  const ops = Object.keys(getOperations(logic))
  if (sort) return ops.sort()
  return ops
}

function nodePathList(logic, path=[]) {
  if (typeof logic === 'string') {
    return [{path: path.join('.'), logic}]
  }
  if (isArray(logic)) {
    const [op, ...args] = logic
    if (op === 'Not' && (typeof args[0] === 'string')) {
      return [{path: path.join('.'), node: logic}]
    } else {
      let result = []
      for (const i in args) {
        result = result.concat(nodePathList(args[i], [...path, +i+1]))
      }
      return result
    }
  }
  return []
}

export {
  allValueCombos,
  variableList,
  operationList,
  nodePathList,
}