import React from 'react'
import ReactVega from 'react-vega'
import ReactVegaLite from 'react-vega-lite'
import { connect } from 'react-redux'
import vegaconfig from './vegaconfig'

const Vega_ = spec => {
  return (
    <ReactVega spec={spec} />
  )
}

const VegaLite_ = spec => {
  return (
    <ReactVegaLite spec={spec} />
  )
}

const mapStateToProps = state => ({
  config: vegaconfig(state.style.graph)
})

export const Vega = connect(mapStateToProps)(Vega_)
export const VegaLite = connect(mapStateToProps)(VegaLite_)