import evaluate from './evaluate'
import truthtable from './truthtable'
import latex from './latex'
import simplify from './simplify'
import drawCircuit, { requiredSpace, simplifyCircuit } from './circuits'
import { variableList, operationList, nodePathList, allValueCombos } from './analyze'

const and = (...args) => ['And', ...args]
const or = (...args) => ['Or', ...args]
const not = (a) => ['Not', a]
const then = (a, b) => ['Then', a, b]
const iff = (a, b) => ['Equiv', a, b]

const circuit = {
  draw: drawCircuit,
  simplify: simplifyCircuit,
  requiredSpace,
}

export {
  and, 
  or, 
  then,
  not,
  iff,
  simplify,
  latex, 
  evaluate, 
  truthtable,
  circuit,
  allValueCombos,
  variableList,
  operationList,
  nodePathList,
}