import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import styles from './styles.module.css'
import { updateGraphStyle } from 'actions/style'
import { setAutoPagination, setPresentationZoom } from 'actions/config'
import { openModal } from 'actions/uistate'
import SingleQuestionView from './SingleQuestionView'
import { SetGeneratorDefaultsPopup } from '../Modals'
import SlideView from './SlideView'

const STORED_QUESTIONS = 50

class PracticeMode extends SlideView {
  static propTypes = {
    generator: PropTypes.func.isRequired,
    updateGraphStyle: PropTypes.func,
    setAutoPagination: PropTypes.func,
  }

  constructor(props) {
    super(props)
    this.state = {
      count: 0,
      index: 0,
      generated: [],
      showAnswer: false,
      scaleFactor: props.scaleFactor || 1.2,
      collapsed: false,
      defaultParams: props.defaultParams,
      modal: 'none'
    }
  }

  componentDidMount() {
    this.props.setAutoPagination(false)
    this.setScaleFactor(this.state.scaleFactor)
    this.clearProblems()
    this.focus()
  }

  clearProblems = () => {
    this.setState({generated: [this.generate()], index: 0})
  }

  generate = () => {
    this.setState({showAnswer: false})
    try {
      const {generator, defaultParams} = this.props
      const question = new generator(defaultParams)
      return question.output()
    } catch(e) {
      console.error(e)
    }
  }

  openOptions = () => this.setState({modal: 'options'})
  closeOptions = () => this.setState({modal: 'none'})
  onChangeOptions = () => {
    setTimeout(() => {
      this.setState({index: this.state.generated.length-1})
      this.next()
    }, 20)
  }

  problemNumber = () => 1 + this.state.index + this.state.count

  current() {
    return this.state.generated[this.state.index]
  }

  next = () => {
    let index = this.state.index + 1
    let count = this.state.count
    let generated = [...this.state.generated]
    while (index >= generated.length) {
      generated = [...generated, this.generate()]
    }
    while (generated.length > STORED_QUESTIONS) {
      index -= 1
      count += 1
      generated = generated.slice(1)
    }
    this.setState({index, count, generated, showAnswer: false})
  }

  previous = () => {
    this.setState({index: Math.max(0, this.state.index-1)})
  }

  canGoBack = () => {
    if (this.state.index > 0) return true;
    return false;
  }

  renderSlide() {
    const content = this.current()
    return (
      <div className={styles.Slide}>
        {content ? (
          <SingleQuestionView 
            content={content}
            scaleFactor={this.state.scaleFactor}
            showAnswer={this.state.showAnswer}
          />
        ) : null}
      </div>
    )
  }

  renderNav() {
    return (
      <div className={styles.Nav}>
        {this.renderNumber()}
        
        <div style={{marginTop: 'auto'}}/>

        {this.renderNavArrows()}
        {this.renderAnswerButton()}
        <button className={styles.NavButton} 
          onClick={this.openOptions} 
          disabled={!this.props.defaultParams}
        >Options</button>
        {this.renderScaleInput()}
        <button 
          className={styles.NavButton} 
          onClick={this.close}
          title="shortcut: Esc"
        >Close</button>
        <SetGeneratorDefaultsPopup 
          isOpen={this.state.modal === 'options'}
          onRequestClose={this.closeOptions}
          onChange={this.onChangeOptions}
        />
      </div>
    )
  }
}

const mapStateToProps = state => ({
  generator: state.generators.generators[state.uistate.generatorSelection],
  defaultParams: state.generators.defaultParams[state.uistate.generatorSelection],
  //graphScaleFactor: state.style.graph.scaleFactor,
  scaleFactor: state.config.presentationZoom / 100,
})
const mapDispatchToProps = dispatch => ({
  setAutoPagination: bool => dispatch(setAutoPagination(bool)),
  setGraphScaleFactor: scaleFactor => dispatch(updateGraphStyle({scaleFactor})),
  openOptions: () => dispatch(openModal('generatorParams')),
  setPresentationScaleFactor: scaleFactor => dispatch(setPresentationZoom(scaleFactor*100)),
})

export default connect(mapStateToProps, mapDispatchToProps)(PracticeMode)