import QGen from './QGen'
import { cloneDeep, includes, set, times } from 'constants/lodash'
import { 
  and, or, not,
  simplify, 
  truthtable, 
  latex,
  circuit,
  variableList,
  operationList,
  nodePathList,
} from './tools/logic'

function randomCircuit(vars, {simplifyRate=0, nonTrivial=true, operations=['And', 'Or']}={}) {
  const rd = this.random
  if (nonTrivial && vars.length > 2) {
    const result = randomCircuit.call(this, vars, {simplifyRate, operations, nonTrivial: false})
    if ((operationList(result).length === 1) && (operations.length > 1)) {
      const [op, ...args] = result
      if (op === 'And' || op === 'Or') {
        return [{And: 'Or', Or: 'And'}[op], ...args]
      }
      if (op === 'Equiv') {
        return [rd.choice(operations.filter(o => o !== op)), ...args]
      }
    }
    return result
  }

  const l = vars.length
  if (l === 1) {
    return vars[0]
  }
  const op = rd.choice(operations)
  if (l === 2) {
    return [op, ...vars]
  }
  const i = rd.randint(1,l-1)
  const topass = {simplifyRate, operations}
  const left = randomCircuit.call(this, vars.slice(0,i), topass)
  const right = randomCircuit.call(this, vars.slice(i), topass)
  
  const args = simplifyRate
    ? [left, right].map(x => rd.random() < simplifyRate ? simplify(x, {recursive: false}) : x)
    : [left, right]
  return [op, ...args]
}

export class WriteCircuit extends QGen {
  static info = {
    name: 'Circuit Formula',
    description: 'Write the formula for a circuit',
    tags: ['logic'],
  }

  static options = {
    singleColumn: true,
  }

  static params = {
    numberOfNodes: {
      type: 'select',
      label: 'Number of variables',
      options: [4, 5, 6, 7, 8, 9, 10, 11]
    }
  }

  static defaultParams = {
    numberOfNodes: 5
  }

  generate(params) {
    const numberOfNodes = params.numberOfNodes
    let letters = [
      ...(numberOfNodes > 11
        ? 'abcdefghijklmnopqrstuvwxyz'
        : this.random.choice(['abcdefghijk', 'pqrstuvwxyz'])
      ).slice(0,numberOfNodes)
    ]
    if (this.random.bool()) {
      letters = letters.reverse()
    }
    times(this.random.randint(0,5), () => {
      const i = this.random.randint(1,numberOfNodes-1)
      letters = [...letters.slice(i), ...letters.slice(0,i)]
    })
    const logic = randomCircuit.call(this, letters, {simplifyRate: .5})
    
    const answer = latex(simplify(logic))
    const w = circuit.requiredSpace(logic).width / 10
    const options = w > 2.5 ? {singleColumn: false} : {}
    return {
      instructions: 'Write a logical expression to represent the circuit.',
      answer: '$$' + answer + '$$',
      diagram: circuit.draw(logic, {width: w + 'in'}),
      options,
    }
  }
}

export class DrawCircuit extends QGen {
  static info = {
    name: 'Draw Circuit',
    description: 'Draw the circuit for a logic formula',
    tags: ['logic'],
  }

  static options = {
    singleColumn: true,
  }

  static params = {
    numberOfNodes: {
      type: 'select',
      label: 'Number of variables',
      options: [3, 4, 5, 6, 7, 8, 9, 10, 11]
    },
    useIfThen: {
      type: 'select',
      label: 'Use if ... then ... ',
      options: ['never', 'sometimes', 'always']
    },
    useEquiv: {
      type: 'select',
      label: 'Use if and only if',
      options: ['never', 'sometimes', 'always']
    }
  }

  static defaultParams = {
    numberOfNodes: 4,
    useIfThen: 'never',
    useEquiv: 'never',
  }

  generate(params) {
    const {numberOfNodes, useIfThen, useEquiv} = params
    let letters = [
      ...(numberOfNodes > 11
        ? 'abcdefghijklmnopqrstuvwxyz'
        : this.random.choice(['abcdefghijk', 'pqrstuvwxyz'])
      ).slice(0,numberOfNodes)
    ]
    if (this.random.bool()) {
      letters = letters.reverse()
    }
    times(this.random.randint(0,5), () => {
      const i = this.random.randint(1,numberOfNodes-1)
      letters = [...letters.slice(i), ...letters.slice(0,i)]
    })
    const operations = ['And', 'Or']
    if (useIfThen !== 'never') {
      operations.push('Then')
    }
    if (useEquiv !== 'never') {
      operations.push('Equiv')
    }

    let logic
    let goodToGo = false
    while (!goodToGo) {
      logic = simplify(randomCircuit.call(this, letters, {simplifyRate: 1, operations}))
      goodToGo = true
      const used = operationList(logic)
      if (useIfThen === 'always' && !includes(used, 'Then')) {
        goodToGo = false
        continue
      }
      if (useEquiv === 'always' && !includes(used, 'Equiv')) {
        goodToGo = false
        continue
      }
    }

    const todraw = circuit.simplify(logic, {combineOr: true})
    const w = circuit.requiredSpace(todraw).width / 10

    const options = numberOfNodes > 7 ? {singleColumn: false} : {}
    return {
      instructions: 'Sketch a circuit to represent the logical expression.',
      question: '$$' + latex(logic) + '$$',
      answer: {
        prompt: ['EmptySpace', {height: '1.5in', width: '1.5in'}],
        correct: circuit.draw(todraw, {width: w + 'in'}),
      },
      options
    }
  }
}


function complicate(logic, {letters, count=1, operations=['And', 'Or']}={}) {
  if (+count === 0) {
    return logic
  }
  if (+count > 1) {
    const result = complicate.call(this, logic, {letters, operations})
    return complicate.call(this, result, {letters, operations, count: +count-1})
  }

  const rd = this.random
  const letters_ = letters || variableList(logic)
  const toPass = {letters: letters_, operations}

  if (typeof logic === 'string') {
    const op = rd.choice(operations)
    const numArgs = {
      And: rd.randint(2,letters.length),
      Or: rd.randint(2,letters.length),
      Not: 1,
      Then: 2,
      Equiv: 2,
    }[op]
    const args = rd.shuffle([
      logic, 
      ...rd.sample(letters_.filter(l => l !== logic), numArgs-1)
    ])
    return complicate.call(this, [op, ...args], toPass)
  }
  const [op, ...args] = logic
  const j = rd.randint(0,args.length-1)
  const newArgs = args.map((x, i) => {
    if (i === j) {
      if (typeof x === 'string') {
        const o = rd.choice(operations.filter(o => o !== op))
        const numArgs = {
          And: rd.randint(2,letters_.length-1),
          Or: rd.randint(2,letters_.length-1),
          Not: 1,
          Then: 2,
          Equiv: 2,
        }[o]
        let a = [x]
        if (numArgs > 1) {
          a = a.concat(rd.sample(letters_.filter(l => l !== x), numArgs-1))
        }

        return [o, ...rd.shuffle(a)] 
      } else {
        return complicate.call(this, x, toPass)
      }
    } else {
      return x
    }
  })
  return [op, ...newArgs]
}

function applyNots(logic, number) {
  const rd = this.random
  let result = cloneDeep(logic)
  let k = number
  while (k > 0) {
    k -= 1
    const nodes = nodePathList(result).filter(n => typeof n.logic === 'string')
    if (nodes.length) {
      const toChange = rd.choice(nodes)
      result = set(result, toChange.path, not(toChange.logic))
    }
  }
  return result
}


export class CircuitTruthTable extends QGen {
  static info = {
    name: 'Circuit Truth Table',
    description: 'Create the truth table for a circuit',
    tags: ['logic'],
  }

  static options = {
    singleColumn: false,
  }

  static params = {
    numberOfVariables: {
      type: 'select',
      label: 'Number of variables',
      options: [2, 3, 4]
    },
    complexity: {
      type: 'select',
      label: 'Complexity',
      options: [0,1,2,3,4,5,6,7]
    }
  }

  static defaultParams = {
    numberOfVariables: 2,
    complexity: 3,
  }

  generate(params) {
    const {numberOfVariables, complexity} = params
    let letters = [...this.random.choice([
      'abcd',
      'fghj',
      'pqrs',
      'wxyz'
    ]).slice(0,numberOfVariables)]
    
    const start = randomCircuit.call(this, letters)
    const logic1 = complicate.call(this, start, {
      letters, 
      operations: ['And', 'Or'], 
      count: complexity,
    })
    const logic = applyNots.call(this, logic1, this.random.randint(1,nodePathList(circuit.simplify(logic1)).length-1)) 
    const w = circuit.requiredSpace(logic).width / 8

    return {
      instructions: 'Complete the truth table for the circuit shown.',
      diagram: circuit.draw(logic, {width: w + 'in'}),
      answer: {
        prompt: truthtable(logic, {empty: true}),
        correct: truthtable(logic, {empty: false})
      },
    }
  }
}

export class TruthTable extends QGen {
  static info = {
    name: 'Truth Table',
    description: 'Complete the truth table for a logic expression',
    tags: ['logic'],
  }

  static options = { singleColumn: true }

  static params = {
    numberOfVariables: {
      type: 'select',
      label: 'Number of variables',
      options: [2,3,4]
    },
    complexity: {
      type: 'select',
      label: 'Complexity',
      options: [0,1,2,3,4,5,6,7]
    },
    allowIfThen: {
      type: 'boolean',
      label: 'Allow if ... then ... '
    },
    allowEquiv: {
      type: 'boolean',
      label: 'Allow if and only if',
    }
  }

  static defaultParams = {
    numberOfVariables: 2,
    complexity: 1,
    allowIfThen: true,
    allowEquiv: true,
  }

  generate(params) {
    const rd = this.random
    const { numberOfVariables, complexity, allowIfThen, allowEquiv } = params
    const letters = [...rd.choice([
      'abcde', 'pqrst'
    ])].slice(0,numberOfVariables)
    const operations = ['And', 'Or']
    if (allowIfThen) {
      operations.push('Then')
    }
    if (allowEquiv) {
      operations.push('Equiv')
    }

    const start = randomCircuit.call(this, letters, {operations})
    const logic = complicate.call(this, start, {operations, count: complexity})
    return {
      instructions: 'Complete the truth-table for the logical expression.',
      question: '$$' + latex(logic) + '$$',
      answer: {
        prompt: truthtable(logic, {empty: true}),
        correct: truthtable(logic, {empty: false})
      }
    }
  } 
}