import { modalMap, popupMap } from 'containers/Modals'

export const OPEN_MODAL = 'OPEN_MODAL'
export const OPEN_POPUP = 'OPEN_POPUP'
export const CLOSE_MODAL = 'CLOSE_MODAL'
export const CLOSE_POPUP = 'CLOSE_POPUP'
export const UPDATE_GENERATOR_SELECTION = 'UPDATE_GENERATOR_SELECTION'
export const SET_SELECTED_ELEMENT = 'SET_SELECTED_ELEMENT'


export const openModal = modal => {
  if (modalMap[modal]) {
    return ({
      type: OPEN_MODAL,
      payload: modal
    })
  }
  if (popupMap[modal]) {
    return ({
      type: OPEN_POPUP,
      payload: modal
    })
  }
}
export const openPopup = popup => {
  if (popupMap[popup]) {
    return ({
      type: OPEN_POPUP,
      payload: popup
    })
  }
}
export const closeModal = () => ({type: CLOSE_MODAL})
export const closePopup = () => ({type: CLOSE_POPUP})

export const updateGeneratorSelection = id => ({
  type: UPDATE_GENERATOR_SELECTION,
  payload: id
})

export const setSelectedElement = id => ({
  type: SET_SELECTED_ELEMENT,
  payload: id,
})