import React from 'react'
import { Circle as SCircle, Rect as SRect, Ellipse as SEllipse } from './react-svgplot'
import { Style } from '.'

export const Circle = props => {
  const {styleName, ...otherprops} = props
  if (styleName) {
    return (
      <Style name={styleName}><SCircle {...otherprops}/></Style>
    )
  }
  return <SCircle {...otherprops}/>
}

export const Rect = props => {
  const {styleName, ...otherprops} = props
  if (styleName) {
    return (
      <Style name={styleName}><SRect {...otherprops}/></Style>
    )
  }
  return <SRect {...otherprops}/>
}

export const Ellipse = props => {
  const {styleName, ...otherprops} = props
  if (styleName) {
    return (
      <Style name={styleName}><SEllipse {...otherprops}/></Style>
    )
  }
  return <SEllipse {...otherprops}/>
}