import React from 'react'
import { connect } from 'react-redux'
import { renderObject, parseObject } from 'rendering'
import { openModal, setSelectedElement } from 'actions/uistate'


const TopLevelElement = props => {

  const divprops = {}
  const classNames = []
  if (props.canEdit) {
    classNames.push('editable')
    divprops.onDoubleClick = () => {
      props.setSelectedElement(props.id);
      props.openEditor()
    }
  }
  if (props.columns) {
    const elemprops = parseObject(props.data).props
    if (elemprops.options && elemprops.options.singleColumn) {
      classNames.push('single-column')
    }
  }

  if (classNames.length) {
    divprops.className = classNames.join(' ')
  }
  return (
    <div {...divprops}>
      {renderObject(props.data)}
    </div>
  )
}

const mapStateToProps = state => ({
  canEdit: state.config.allowEditing,
  columns: state.style.columns,
})
const mapDispatchToProps = dispatch => ({
  openEditor: () => dispatch(openModal('editor')),
  setSelectedElement: id => dispatch(setSelectedElement(id)),
})

export default connect(mapStateToProps, mapDispatchToProps)(TopLevelElement)