import { isEqual } from 'constants/lodash'
import { and, or, not } from '.'
const isArray = Array.isArray

function doubleNegative(logic) {
  // ~~A >> A
  try {
    const [a, [b, c]] = logic
    if (a === 'Not' && b === 'Not') {
      return c
    }
  } catch(e) {}
  return logic
}

function deMorgans(logic) {
  // ~(A & B) >> ~A | ~B
  // ~(A | B) >> ~A & ~B
  try {
    const [a, [b, ...c]] = logic
    if (a === 'Not') {
      if (b === 'And') {
        return or(...c.map(not))
      }
      if (b === 'Or') {
        return and(...c.map(not))
      }
    }
  } catch(e) {}
  return logic
}

function ifThenToOr(logic) {
  // A -> B >> ~A | B
  try {
    const [a, b, c] = logic
    if (a === 'Then') {
      return or(not(b), c)
    }
  } catch(e) {}
  return logic
}

function iffToOr(logic) {
  // A <=> B >> (A & B) | (~A & ~B)
  try {
    const [a, ...b] = logic
    if (a === 'Equiv') {
      return or(and(...b), and(...b.map(not)))
    }
  } catch(e) {}
  return logic
}

function combineAnd(logic) {
  // A & (B & C) => A & B & C
  try {
    const [a, ...b] = logic
    if (a === 'And') {
      let args = []
      for (const c of b) {
        if (isArray(c) && c[0] === 'And') {
          args = args.concat(c.slice(1))
        } else {
          args.push(c)
        }
      }
      return and(...args)
    }
  } catch(e) {}
  return logic
}

function combineOr(logic) {
  // A | (B | C) >> A | B | C
  try {
    const [a, ...b] = logic
    if (a === 'Or') {
      let args = []
      for (const c of b) {
        if (isArray(c) && c[0] === 'Or') {
          args = args.concat(c.slice(1))
        } else {
          args.push(c)
        }
      }
      return or(...args)
    }
  } catch(e) {}
  return logic
}

function applyRecursively(logic, operation) {
  const result = operation(logic)
  if (typeof result === 'string') {
    return result
  }
  if (isArray(result)) {
    const [op, ...args] = result
    return [op, ...args.map(a => applyRecursively(a, operation))]
  }
}

function applyRepeatedly(logic, operation) {
  let prev
  let result = logic
  while (!isEqual(prev, result)) {
    prev = result
    result = applyRecursively(result, operation)
  }
  return result
}

function simplify(logic, ops={doubleNegative: true}) {
  let prev, result = logic
  while (!isEqual(prev, result)) {
    prev = result
    if (ops.doubleNegative) result = applyRepeatedly(result, doubleNegative)
    if (ops.ifThen) result = applyRepeatedly(result, ifThenToOr)
    if (ops.iff) result = applyRepeatedly(result, iffToOr)
    if (ops.deMorgans) result = applyRepeatedly(result, deMorgans)
    if (ops.combineAnd) result = applyRepeatedly(result, combineAnd)
    if (ops.combineOr) result = applyRepeatedly(result, combineOr)
  }
  return result
}

export default simplify
export {
  deMorgans,
  doubleNegative,
  combineAnd,
  combineOr,
  iffToOr,
  ifThenToOr,
}