function makeConfig(styles) {
  return {
    area: {
      strokeWidth: 0,
      fillOpacity: styles.fillOpacity,
    },
    line: {
      fillOpacity: 0,
    },
    axis: {
      gridColor: styles.gridColor,
      gridWidth: styles.gridWidth,
      gridOpacity: styles.gridOpacity,
    },
    style: {
      primary: {
        stroke: styles.functionStroke1,
        fill: styles.functionStroke1,
      },
      secondary: {
        stroke: styles.functionStroke2,
        fill: styles.functionStroke2,
      },
      tertiary: {
        stroke: styles.functionStroke3,
        fill: styles.functionStroke3,
      },
      asymptote: {
        stroke: styles.asymptoteColor,
        strokeWidth: styles.asymptoteWidth,
        strokeOpacity: styles.asymptoteOpacity,
        strokeDash: '4 3',
      },
      dashed: {
        strokeDash: '5 3',
      },
      function: {
        strokeWidth: styles.functionStrokeWidth,
      },
    }
  }
}

export default makeConfig