import { addGenerator } from 'actions/generators'
import {
  geometry, 
  logic,
  polynomials,
  othergenerators, 
  transformations,
  trigonometry,
  picofermi,
  //bonusquestions,
  //generatortests,
  //simpleexamples,
} from 'questions'


const modules = {
  geometry,
  logic,
  polynomials,
  othergenerators,
  transformations,
  trigonometry,
  picofermi,
  //bonusquestions,
  //generatortests,
  //simpleexamples,
}

const sorting = {
  picofermi: {sort: 1, prefix: '* '},
  bonusquestions: {sort: 1, prefix: 'Bonus: '},
  generatortests: {sort: 2, prefix: 'Test: '},
  simpleexamples: {sort: 2, prefix: 'Example: '},
}

export function addGenerators(store, {modules}) {
  for (const m in modules) {
    const {sort=0, prefix=''} = sorting[m] || {}
    if (sort >= 0) {
      for (const g in modules[m]) {
        const generator = modules[m][g]
        const id = `${m}.${g}`
        if (generator.register) {
          const {name, description, tags, params, defaultParams} = generator.register()
          //result[id] = {id, generator, name, description, tags, params, defaultParams}
          store.dispatch(addGenerator({
            id, generator, 
            info: {name, description, tags, sort, prefix}, 
            params, defaultParams,
          }))
        }
      }
    }
  }
}

function initializeGenerators(store) {
  addGenerators(store, {modules})
}

export default initializeGenerators