import React from 'react'
import { Vega } from './Vega'
import { connect } from 'react-redux'
import { range } from 'constants/lodash'

function anchoredRange(start, end, step, anchor=0) {
  if (start < anchor && end > anchor) {
    return [...range(-anchor-step, -start+step, step).map(n => -n).reverse(), ...range(anchor, end+step, step)]
  }
  if (anchor < start) {
    return range(anchor, end+step, step).filter(n => n >= start)
  }
  if (anchor > end) {
    return range(-anchor, -start+step).reverse().map(n => -n).filter(n => n <= end)
  }
}

function makeGrid(props) {
  const { scale, domain, values, step } = props
  
  return {
    scale,
    orient: {x: 'bottom', y: 'left'}[scale],
    domain: false,
    values: values ? values : step ? anchoredRange(domain[0], domain[1], step, 0) : undefined,
    labels: false,
    ticks: false,
    tickCount: domain[1] - domain[0] + 1,
    grid: true,
  }
}

function makeAxis(props) {
  const { scale, axisColor, axisOpacity, axisWidth } = props
  return {
    scale,
    orient: scale === 'x' ? 'bottom' : 'left',
    domain: false,
    labels: false,
    ticks: false,
    values: [0],
    grid: true,
    gridColor: axisColor,
    gridOpacity: axisOpacity,
    gridWidth: axisWidth,
  }
}

function makeLabels(props) {
  const { scale, title, labels=false } = props
  return {
    scale,
    orient: scale === 'x' ? 'bottom' : 'left',
    title,
    ...(labels ? {
      labels: true,
      labelOverlap: 'parity',
      labelPadding: 5,
      labelSeparation: 50,
      ticks: true
    } : {labels: false, ticks: false})
  }
}

function makeAxes({
  domain,
  range,
  xTitle,
  yTitle,
  xLabels=false,
  yLabels=false,
  axis=true,
  grid=true,
  axisColor='black',
  axisOpacity,
  axisWidth=1,
}) {

  let result = []
  if (grid) {
    result.push(makeGrid({scale: 'x', domain: domain}))
    result.push(makeGrid({scale: 'y', domain: range}))
  }
  if (axis) {
    result.push(makeAxis({scale: 'x', domain: domain, axisColor, axisOpacity, axisWidth}))
    result.push(makeAxis({scale: 'y', domain: range, axisColor, axisOpacity, axisWidth}))
  }
  if (xLabels || xTitle) {
    result.push(makeLabels({scale: 'x', labels: xLabels, title: xTitle}))
  }
  if (yLabels || yTitle) {
    result.push(makeLabels({scale: 'y', labels: yLabels, title: yTitle}))
  }
  return result
}

function makeScales({domain, range}) {
  return [
    {
      name: "x",
      type: "linear",
      domain: domain,
      range: 'width',
      zero: true,
    },
    {
      name: "y",
      type: "linear",
      domain: range,
      range: 'height',
      zero: true,
    },
  ]
}

function markDef({type, domain, range}) {
  switch(type) {
    case "area-below": return {
      type: 'area',
      encode: {
        enter: {
          x: {scale: 'x', field: 'x'},
          y: {scale: 'y', value: range[0]},
          y2: {scale: 'y', field: 'y'},
        }
      }
    }
    case "area-above": return {
      type: 'area',
      encode: {
        enter: {
          x: {scale: 'x', field: 'x'},
          y: {scale: 'y', field: 'y'},
          y2: {scale: 'y', value: range[1]},
        }
      }
    }
    default: return {
      type: 'line',
      encode: {
        enter: {
          x: {scale: 'x', field: 'x'},
          y: {scale: 'y', field: 'y'},
        }
      }
    }
  }
}

/////////////////////////////////////////////////////////////
/////////////////////////////////////////////////////////////
/////////////////////////////////////////////////////////////

const VegaPlot = props => {
  const {domain=[-10,10], range=[-10,10], width=180, height=180, paths=[]} = props
  const plotstyle = {...props.defaultStyle, ...props.style}
  const scales = makeScales({domain, range})
  const axes = makeAxes({...plotstyle, domain, range})

  const data = {
    "$schema": "https://vega.github.io/schema/vega/v4.json",
    height,
    width,
    padding: 10,
  
    data: paths.map(({values}, i) => ({name: i, values})),

    scales: scales,
    axes: axes,

    marks: paths.map(({style, type}, i) => ({
        style: style,
        from: {data: i},
        ...markDef({type, domain, range})
    })),
  }

  return <Vega {...data} />
}


export default VegaPlot