import React from 'react'
import { connect } from 'react-redux'
import { round, range } from 'constants/lodash'
import { RenderComponent, renderObject } from 'rendering'

function anchoredRange(start, end, step, anchor=0) {
  if (start < anchor && end > anchor) {
    return [...range(-anchor-step, -start+step, step).map(n => -n).reverse(), ...range(anchor, end+step, step)]
  }
  if (anchor < start) {
    return range(anchor, end+step, step).filter(n => n >= start)
  }
  if (anchor > end) {
    return range(-anchor, -start+step).reverse().map(n => -n).filter(n => n <= end)
  }
}


function makeGrid(props) {
  const { scale, domain, values, step, gridColor, gridOpacity, gridWidth } = props
  
  return {
    scale,
    orient: {x: 'bottom', y: 'left'}[scale],
    domain: false,
    values: values ? values : step ? anchoredRange(domain[0], domain[1], step, 0) : undefined,
    labels: false,
    ticks: false,
    tickCount: domain[1] - domain[0] + 1,
    grid: true,
    gridColor,
    gridOpacity,
    gridWidth,
  }
}

function makeAxis(props) {
  const { scale, axisColor, axisOpacity, axisWidth } = props
  return {
    scale,
    orient: scale === 'x' ? 'bottom' : 'left',
    domain: false,
    labels: false,
    ticks: false,
    values: [0],
    grid: true,
    gridColor: axisColor,
    gridOpacity: axisOpacity,
    gridWidth: axisWidth,
  }
}

function makeLabels(props) {
  const { scale, orient, title, labels=false, gridColor, gridOpacity } = props
  return {
    scale,
    orient,
    title,
    ...(labels ? {
      labels: true,
      labelOverlap: 'parity',
      labelPadding: 5,
      labelSeparation: 50,
      tickColor: gridColor,
      tickOpacity: gridOpacity,
    } : {labels: false, ticks: false})
  }
}

function makeAxes({
  domain,
  range,
  xTitle,
  yTitle,
  xLabels=false,
  yLabels=false,
  axis=true,
  grid=true,
  axisColor='black',
  axisOpacity,
  axisWidth=1,
  gridColor='#888',
  gridOpacity,
  gridWidth=1,
}) {

  let result = []
  if (grid) {
    result.push(makeGrid({scale: 'x', domain: domain, gridColor, gridOpacity, gridWidth}))
    result.push(makeGrid({scale: 'y', domain: range, gridColor, gridOpacity, gridWidth}))
  }
  if (axis) {
    result.push(makeAxis({scale: 'x', domain: domain, axisColor, axisOpacity, axisWidth}))
    result.push(makeAxis({scale: 'y', domain: range, axisColor, axisOpacity, axisWidth}))
  }
  if (xLabels || xTitle) {
    result.push(makeLabels({scale: 'x', labels: xLabels, title: xTitle}))
  }
  if (yLabels || yTitle) {
    result.push(makeLabels({scale: 'y', labels: yLabels, title: yTitle}))
  }
  return result
}

function makeScales({domain, range}) {
  return [
    {
      name: "x",
      type: "linear",
      domain: domain,
      range: 'width',
      zero: true,
    },
    {
      name: "y",
      type: "linear",
      domain: range,
      range: 'height',
      zero: true,
    },
  ]
}

function makePath(points) {
  const result = []
  for (const p of points) {
    const [x, y] = isArray(p) ? p : [p.x, p.y]
    result.push({x, y})
  }
  return result
}

const isArray = Array.isArray

const VegaTest = props => {

  const {domain=[-10,10], range=[-10,10], width=150, height=150, paths=[]} = props
  const style = {...props.defaultStyle, ...props.style}
  const scales = makeScales({domain, range})
  const axes = makeAxes({...style, domain, range})

  let pathId = 0
  const thepaths = []
  for (const path of paths) {
    const values = makePath(path.points)
    thepaths.push({id: ++pathId, values, style: path.style})
  }

  const data = {
    "$schema": "https://vega.github.io/schema/vega/v4.json",
    height,
    width,
    padding: 10,
    data: thepaths.map(p => ({name: p.id, values: p.values})),
    scales: scales,
    axes: axes,
    marks: thepaths.map(p => {
      const pathstyle = {}
      for (const k in p.style) {
        pathstyle[k] = {value: p.style[k]}
      }
      return ({
        type: 'line',
        from: {data: p.id},
        encode: {
          enter: {
            x: {scale: 'x', field: 'x'},
            y: {scale: 'y', field: 'y'},
            ...pathstyle,
          }
        }
      })
    }),
  }

  return <RenderComponent data={['Vega', data]} />
}



export default connect(
  state => ({defaultStyle: state.style.graph})
)(VegaTest)