import React from 'react'
import PropTypes from 'prop-types'
import styles from './styles.module.css'
import { round } from 'constants/lodash'


const KEYMAP = {
  answer: 'Tab',
  collapse: '\\',
  next: 'ArrowRight',
  previous: 'ArrowLeft',
  incZoom: 'ArrowUp',
  decZoom: 'ArrowDown',
}

class SlideView extends React.Component {
  static propTypes = {
    updateGraphStyle: PropTypes.func,
    setAutoPagination: PropTypes.func,
  }

  constructor(props) {
    super(props)
    this.state = {
      showAnswer: false,
      scaleFactor: props.scaleFactor || 1.2,
      collapsed: false,
    }
  }

  componentDidMount() {
    this.props.setAutoPagination(false)
    this.setScaleFactor(this.state.scaleFactor)
    this.focus()
  }
  componentWillUnmount() {
    this.props.setPresentationScaleFactor(this.state.scaleFactor)
  }
  close = () => {
    this.props.setPresentationScaleFactor(this.state.scaleFactor)
    this.props.setGraphScaleFactor(1)
    this.props.setAutoPagination(true)
    this.props.onRequestClose()
  }

  showAnswer = () => this.setState({showAnswer: true})
  hideAnswer = () => this.setState({showAnswer: false})
  toggleAnswer = () => this.setState({showAnswer: !this.state.showAnswer})
  setScaleFactor(scaleFactor) {
    this.props.setGraphScaleFactor(scaleFactor)
    this.setState({scaleFactor})
  }
  changeScaleFactor = evt => this.setScaleFactor(evt.target.value)
  increaseScaleFactor() {
    const scaleFactor = round(this.state.scaleFactor + .2, 1)
    this.setScaleFactor(scaleFactor)
  }
  decreaseScaleFactor() {
    const scaleFactor = Math.max(.2, round(this.state.scaleFactor - .2, 1))
    this.setScaleFactor(scaleFactor)
  }

  onKeyDown = evt => {
    //evt.preventDefault()
    switch(evt.key) {
      case KEYMAP.next: this.trynext(); evt.preventDefault(); return;
      case KEYMAP.previous: this.tryprevious(); evt.preventDefault(); return;
      case KEYMAP.incZoom: this.increaseScaleFactor(); evt.preventDefault(); return;
      case KEYMAP.decZoom: this.decreaseScaleFactor(); evt.preventDefault(); return;
      case KEYMAP.collapse: this.toggleCollapse(); evt.preventDefault(); return;
      case KEYMAP.answer: this.toggleAnswer(); evt.preventDefault(); return;
      default: return false;
    }
  }

  focus() { document.getElementById('slideshow-view').focus() }

  toggleCollapse = () => {
    this.setState({collapsed: !this.state.collapsed})
    this.focus()
  }
  unCollapse = () => this.state.collapsed ? this.toggleCollapse() : null
  
  renderAnswerButton() {
    if (this.state.showAnswer) {
      return (
        <button className={styles.NavButton} 
          title={`shortcut: ${KEYMAP.answer}`}
          onClick={this.hideAnswer}>
          Show Question
        </button>
      )
    } else {
      return (
        <button className={styles.NavButton} 
          title={`shortcut: ${KEYMAP.answer}`}
          onClick={this.showAnswer}>
          Show Answer
        </button>
      )
    }
  }

  canGoForward = () => true;
  canGoBack = () => true;
  trynext = () => {
    if (this.canGoForward()) {
      this.next()
    }
  }
  tryprevious = () => {
    if (this.canGoBack()) {
      this.previous()
    }
  }

  renderNavArrows() {
    const backClassNames = [styles.ArrowButton, styles.ArrowButtonLeft]
    const fwdClassNames = [styles.ArrowButton, styles.ArrowButtonRight]
    if (!this.canGoBack()) {
      backClassNames.push(styles.ArrowButtonDisabled)
    }
    if (!this.canGoForward()) {
      fwdClassNames.push(styles.ArrowButtonDisabled)
    }
    return (
      <div className={styles.ArrowButtonContainer}>
        <a className={backClassNames.join(' ')}
          title={`shortcut: ${KEYMAP.previous}`}
          onClick={this.tryprevious}/>
        <a className={fwdClassNames.join(' ')}
          title={`shortcut: ${KEYMAP.next}`}
          onClick={this.trynext}/>
      </div> 
    )
  }

  renderScaleInput() {
    return (
      <div>
        Scale: 
        <input type="number" min=".2" step=".2"
          value={this.state.scaleFactor} 
          autoFocus={true}
          title={`shortcut: ${KEYMAP.decZoom} / ${KEYMAP.incZoom}`}
          onChange={this.changeScaleFactor}/>     
      </div>
    )
  }

  renderSlide() {
    return <div className={styles.Slide}/>
  }

  renderCollapse() {
    return (
      <div className={styles.Collapse}
        title={`shortcut: ${KEYMAP.collapse}`}
        onClick={this.toggleCollapse}>
        <a className={styles.ArrowButton + ' ' + styles.CollapseButton}
          title={`shortcut: ${KEYMAP.collapse}`}
          onClick={this.toggleCollapse}/>
      </div>
    )
  }

  renderNumber() {
    if (this.problemNumber) {
      return (
        <div style={{fontSize: '3em', fontWeight: 'bold', textAlign: 'center'}}>
          {this.problemNumber()}
        </div>
      )
    }
    return null
  }

  renderNav() {
    return (
      <div className={styles.Nav}>
        {this.renderNumber()}
        <div style={{marginTop: 'auto'}}/>
        {this.renderNavArrows()}
        {this.renderAnswerButton()}
        {this.renderScaleInput()}
        <button className={styles.NavButton} 
          onClick={this.close}
          title="shortcut: Esc"
        >Close</button>
      </div>
    )
  }

  render() {
    const className = this.state.collapsed
      ? styles.PresentationView + ' ' + styles.Collapsed
      : styles.PresentationView
    return (
      <div className={className}
        tabIndex="0"
        id="slideshow-view"
        onKeyDown={this.onKeyDown}
      >
        {this.renderNav()}
        {this.renderCollapse()}
        {this.renderSlide()}
      </div>
    )
  }
}

export default SlideView

