import evaluate from './evaluate'
import { allValueCombos, variableList } from './analyze'

function truthTable(logic, {empty=false}={}) {
  const rows = allValueCombos(logic)
  const letters = variableList(logic, {sort: true})
  return [
    'Table', {border: true, align: new Array(letters.length+1).fill('c').join('|')},
    ['TRow', {header: true}, ...letters.map(l => '$$' + l + '$$'), 'T/F'],
    ...rows.map(r => {
      const t = evaluate(logic, r)
      return [
        'TRow', {border: 'top'}, 
        ...letters.map(l => r[l] ? 'T' : 'F'),
        empty ? ['EmptySpace', {height: '7mm', width: '7mm'}] : (t ? 'T' : 'F')
      ]
    })
  ]
}

export default truthTable